.account {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: var(--twitter-trends-background);
}

.account:hover {
    background-color: #EFF1F1;
}

.account__follow {
    font-size: 12px !important;
    font-weight: 800 !important;
    border-radius: 30px !important;
    color: #fff !important;
    background-color: #272C30 !important;
}

.account div > a, .account div > p {
    font-size: 13px;
}