.termsOfService {
    display: flex;
    padding: 10px 20px;
    font-size: 13px;
    color: #8E99A1;
}

.termsOfService a {
    margin-right: 10px;
    color: #8E99A1;
    text-decoration: none;
}

.termsOfService p {
    display: inline;
}

.termsOfService a:hover {
    text-decoration: underline;
}

