.trend {
    display:flex;
    flex-direction: column;
    background-color: var(--twitter-trends-background);
    padding: 10px 20px;
}

.trend:hover {
    background-color: #EFF1F1;
}

.trend__topic > h3 {
    font-size: 15px;
}

.trend__category {
    display: flex;
    justify-content: space-between;
}

.trend__category > p, .trend__tweets > p {
    font-size: 13px;
}