
.post {
    display: flex;
    padding: 20px 20px 20px 0px;
    border-bottom: 1px solid var(--twitter-background);
}  

.post:hover {
    background-color: var(--twitter-postHover-background);
}

.post__body > img {
    border-radius: 15px;
    width: 100%;
}

.post__avatar {
    margin-left: 20px;
    margin-right: 20px;
}

.post__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post__headerSpecial {
    font-weight: 600;
    font-size: 15px;
    color: gray;
}

.post__headerDescription {
    margin-bottom: 10px;
}