.trends__header {
    display: flex;
    justify-content: space-between;
    background-color: var(--twitter-trends-background);
    margin-top: 15px;
    padding: 10px 20px;
    border-radius: 15px 15px 0px 0px;
}

.trends__header > h2 {    
    font-size: 18px;
    font-weight: 800 !important;
}

.trends__showMore {
    color: var(--twitter-color);
    background-color: var(--twitter-trends-background);
    border-radius: 0px 0px 15px 15px;
    padding: 10px 20px;
}

.trends__showMore:hover {
    background-color: #EFF1F1;
}