.sidebarOption {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarOption:hover {
    background-color: #E7E7E8;
    border-radius: 30px;  
    transition: 100ms ease-in-out; /*
    color: var(--twitter-color); */
}

.sidebarOption > .MuiSvgIcon-root {
    padding: 20px;
}

.sidebarOption > h3 {
    font-weight: 400;
    font-size: 20px;
    margin-right: 20px;
}

.sidebarOption--active {
    color: var(--twitter-color);
}

.sidebarOption--active > h3 {
    font-weight: 800;
}