.search {
    display: flex;
    flex: 0.3;
    align-items: center;
    border-radius: 30px;
    background-color: var(--twitter-background); 
}

.search:active {
    border: 1px solid var(--twitter-color);
    pointer-events: auto;
}

.widgets__SearchIcon {
    margin: 0 20px;
}
.search > input {
    background-color: var(--twitter-background);
    border: none;
    outline: none;       
    padding: 15px 0px;
}

.widgets__cancelIcon {
    display: flex;
    flex: 1;
}